	module.exports = {"data":{"markdownRemark":{"html":"<p>\"중독 된\"음모가 발전 중이며 이혼했습니다. 너무 많은 사랑은 좋은 것이 아니라 사랑이 많은 것이지, 좋은 것이 아니라 사랑이 아니라 좋은 것이 아니라 무엇을해야할까요?</p>\n<p>C Jun이 \"Lion City의 Apocalypse\"원고를 토론하기 위해 왔고 그와 함께 토론했습니다. 그는 최근 베이징에 가서 어떤 사람들은 현명한 삶을 살았다 고 말하면서 아래 사람들 만이 열심히 일하고 있다고 말했다. 나는 중국 사회의 발전이 열심히 일하는 사람들을 갖는 것이라고 생각한다. 모두가 똑똑하다면,이 사회는 개선 될 수 없습니다. 고도로 이야기하는 사람이 있어야하지만, 사회에서 더 중요한 것은 사람들이 하나씩 일을 처리한다는 것입니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0024.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0024.md","frontmatter":{"title":"2월 4일 금요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0024.md absPath of file >>> MarkdownRemark"}}

